@use 'base/variables' as v;
@use 'base/mixins' as m;

.header {
    &.fijoMovil{
        @media (max-width: 480px){
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            z-index: 2;
            box-shadow: .1rem .2rem .3rem #333;
            opacity: 100;
        }
    }
    &.fijo{
        top: 0;
        left: 0;
        position: fixed;
        width: 100%;
        box-shadow: .1rem .2rem .3rem #333;
    }
    @include m.tablet {
        &.fijo{
        position: fixed;
        width: 100%;
        box-shadow: .1rem .2rem .3rem #333;
    }
    }
    
    background-color: v.$verde;
    h1 {
        font-size: 2.2rem;
        color: v.$blanco;
        text-align: center;
        margin-bottom: 3px;
        margin-top: 1rem;
        
        @include m.tablet {
            margin: 0;
            font-size: 3rem;
        }
        @include m.desktop {
            font-size: 4rem;
        }
    }
    .contenido-header {
        display: flex;
        justify-content: center;
        padding: 0rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            gap: 2rem;
            align-items: center;
            padding: 2rem;
        }
    }
}
