.boton{
    text-align: center;
    display: flex;
    justify-content: center;
    @include m.tablet {
        display: none;
    }
}
.abrir{
    height: 48px;
    position: relative;
    transition: transform .4s;
    width: 48px;
    @media (max-width: 320px){
        margin-left: -50px;
    }
    span {
        background-color: #fff;
        display: block;
        height: 2px;
        left: 14px;
        margin-top: -1px;
        position: absolute;
        top: 50%;
        transition: .4s;
        width: 20px;
        &:first-child { transform: translateY(-6px) }
        &:last-child { transform: translateY(6px) }
  }
  
  &.active span {
    &:first-child { transform: rotate(45deg) translate(0) }
    &:nth-child(2) { transform: scaleX(0) }
    &:last-child { transform: rotate(-45deg) translate(0) }
  }
}


#navigation:target .navegacion-principal{
    height: 12rem;
    background: none repeat scroll 0 0;
    transition: height 0.4s;
    @include m.tablet {
        height: 5.6rem;
        overflow: visible;
    }
}
#b:target .navegacion-principal{
    height: 9rem;
    background: none repeat scroll 0 0;
    transition: height 0.4s;
    @include m.tablet {
        height: 5.6rem;
        overflow: visible;
    }
}


.navegacion-principal {
    margin-top: 1rem;
    clear: both;
    height: 0;
    overflow: hidden;
    transition: height 0.4s;
    @include m.tablet {
        margin-top: 0;
        height: auto;
        overflow: visible;
        display: flex;
        gap: 2rem;
    }
    a {
        @include m.tablet {
            margin: 0;
        }
        display: block;
        text-align: center;
        color: v.$blanco;
        font-size: 2rem;
        margin-bottom: 1rem;
        &:hover {
            color: v.$amarillo;
        }
    }
}
