@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    padding: 3rem 0;
    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    @include m.resetear-lista;
    @include m.grid(2, 2rem);
    @include m.telefono {
        @include m.grid(3, 2rem)
    };
    @include m.tablet {
        @include m.grid(4, 2rem)
    };
    @include m.desktop {
        @include m.grid(6, 2rem)
    };
};
.pointer {
    &:hover{
        cursor: pointer;
    }
}

