.overlay {
    @include m.tablet {
        display: grid;
        place-items: center;
    }
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;

    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / .5);
    picture {
        display: block;
        max-width: 70rem;
    }
}
.btn-cerrar {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$negro;
    font-weight: 700;
    display: grid;
    place-items: center;
    &:hover{
        cursor: pointer;
    }
}