@use 'base/variables' as v;
@use 'base/mixins' as m;


.boletos {
    text-align: center;
    padding: 5rem 0;
    h3 {
        color: v.$blanco;
        margin-bottom: 3rem;
    }
}
.contenedor-boletos {
    @include m.tablet {
        @include m.grid(2, 2rem)
    }
}
.pase {
    .nombre-pase{
        font-size: 2.8rem;
        margin-top: 2rem;
    }
    p {
        color: v.$blanco;
        margin: 0;
    }
    // se puede poner & y hereda .pase
    &-header {
        background-color: v.$rosa;
        padding: 1rem 0;
        margin-top: 2rem;
        p {
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }
    .pase-body {
        background-color: v.$blanco;
        ul {
            @include m.resetear-lista;
            li,
            :nth-child(odd) {
                background-color: v.$blanco;
                padding: 1rem;
                text-align: start;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    .pase-footer {
        background-color: v.$morado;
        padding: 1rem;
        p {
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}
