@use 'variables' as v;
@use 'base/mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    // toda la funcion javascript scrollnav() se hace en css así
  //  scroll-behavior: smooth;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    &.fijar-body {
        overflow: hidden;
    }
    @media (max-width: 6000px) {
        &.body-scroll{
            padding-top: 8.6rem;
        }
    }
    
}
@media (max-width: 780px){
    body.body-scroll {
        padding-top: 7.45rem;
    }
}
@media (max-width: 520px){
    body.body-scroll {
        padding-top: 0;
    }
}
p {
    font-size: 2rem;
    color: v.$negro;
    line-height: 1.5;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
h1,h2,h3 {
    margin: 0 0 5rem 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}