@use 'base/variables' as v;
@use 'base/mixins' as m;

.video {
    position: relative;
    max-height: 70rem;
    min-height: 50rem;
    overflow: hidden;
    .overlay {
        position: absolute;
        background: rgb(118,0,163);
        background: linear-gradient(270deg, rgba(118,0,163,0.5) 0%, rgba(255,0,112,0.3) 100%);
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    video {
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%;
        }
    }
}
.contenido-video {
    z-index: 1;
    h2{
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
    }
    p{
        color: v.$blanco;
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        font-size: 2.4rem;
        font-weight: 700;
        margin: 0;
    }
}